import React from 'react';
import { useMutation, useQueryClient } from 'react-query';
import { handlePurchase } from '../../api';
import { useInsurance } from '../../context/Insurance';
import shade from '../../utils/shade';
import CardLoader from '../CardLoader';

export default function Notifications({
  logo,
  customerID,
  customerName,
  show,
  dispatchWidget,
  color,
}) {
  const { quotes, latestQuote, latestPolicy } = useInsurance();

  const queryClient = useQueryClient();
  const createPolicy = useMutation(handlePurchase, {
    onSuccess: () => {
      dispatchWidget({ type: 'CLEAR_NOTIFICATION' });
      queryClient.invalidateQueries('customer');
    },
  });

  const purchaseQuote = () => {
    if (latestQuote?.SecondaryID)
      createPolicy.mutate({
        quoteId: latestQuote?.SecondaryID,
        customerID,
        customerName,
      });
  };

  const getPurchasedQuote = () => {
    let latestQuoteIndex = quotes.findIndex(
      (q) => q?.SecondaryID === latestPolicy?.Data?.quoteID,
    );
    if (latestQuoteIndex !== -1) return quotes[latestQuoteIndex];
  };

  const purchasedQuote = getPurchasedQuote();

  if (createPolicy?.isLoading)
    return <CardLoader color={color} text="Generating quote..." />;

  return (
    <div className="pb-5 y-2 mx-4 my-auto">
      {/* {show && purchasedQuote && (
        <div
          className={`rounded-3xl mx-2 mt-30 pl-1 pr-1 pb-8 pt-4 bg-white my-2 flex flex-col justify-center`}
        >
          <div className="flex flex-row items-center justify-start">
            {logo && (
              <img src={logo} alt="logo" className="ml-10 getInsuredImg" />
            )}
            <span
              className={`text-2xl font-semibold font-bold text-center w-full ${
                !logo && 'mb-4'
              }`}
            >
              Your Current Quote
            </span>
          </div>
          <div>
            <span className="text-5xl font-bold	text-green-500">
              &#163; {purchasedQuote?.Data?.quoteExpense?.toFixed(2)}
            </span>
          </div>
          <div className="mt-3">
            <span className="text-base text-gray-500">
              The protection you need, when you need it
            </span>
          </div>

          <button
            className={`rounded-2xl bg-primary opacity-50 ripple text-primary font-bold mt-4 tracking-lighter px-8 py-4`}
            style={{ backgroundColor: shade(color, 0.9) }}
            onClick={() => {}}
            disabled
          >
            Purchased
          </button>
        </div>
      )} */}
      {show &&
        latestQuote?.Data &&
        purchasedQuote?.SecondaryID !== latestQuote?.SecondaryID && (
          <div
            className={`rounded-3xl mx-2 mt-30 pl-1 pr-1 pb-8 pt-4 bg-white my-2 flex flex-col justify-center`}
          >
            <div className="flex flex-row items-center justify-start">
              {logo && (
                <img src={logo} alt="logo" className="ml-10 getInsuredImg" />
              )}
              <span
                className={`text-2xl font-semibold font-bold text-center w-full ${
                  !logo && 'mb-4'
                }`}
              >
                New Quote Available
              </span>
            </div>
            <div>
              <span className="text-5xl font-bold	text-green-500">
                &#163; {latestQuote?.Data?.quoteExpense?.toFixed(2)}
              </span>
            </div>
            <div className="mt-3">
              <span className="text-base text-gray-500">
                The protection you need, when you need it
              </span>
            </div>

            <button
              className={`rounded-2xl bg-primary ripple text-primary font-bold mt-4 tracking-lighter px-8 py-4`}
              style={{ backgroundColor: shade(color, 0.9) }}
              onClick={() => purchaseQuote()}
            >
              Purchase Policy
            </button>
          </div>
        )}

      {!show && (
        <div
          style={{
            height: '100%',
            textTransform: 'uppercase',
            fontWeight: 'bold',
            letterSpacing: 1,
            color,
          }}
        >
          No new notifications
        </div>
      )}
    </div>
  );
}
