import React, { useReducer, Suspense, useEffect } from 'react';
import {
  useQuery,
  useMutation,
  QueryClient,
  QueryClientProvider,
} from 'react-query';
import { faBell } from '@fortawesome/free-solid-svg-icons';

import { compareAsc } from 'date-fns';
import { customerReducer, widgetReducer } from './reducers';
import Policy from './components/Tabs/Policy';
import {
  createCustomer as createCustomerAPI,
  createQuote as createQuoteAPI,
  getCustomer,
} from './api';
import { InsuranceProvider, useInsurance } from './context/Insurance';
import Notifications from './components/Tabs/Notifications';
import Claims from './components/Tabs/Claims';

const Tab = React.lazy(() => import('./components/Tab'));
const Footer = React.lazy(() => import('./components/Footer'));

const queryClient = new QueryClient();

function App(props) {
  const {
    quotes,
    latestQuote,
    latestPolicy,
    dispatch: dispatchInsurance,
  } = useInsurance();
  const createCustomer = useMutation(createCustomerAPI, {
    onSuccess: () => {
      createNewQuote();
    },
  });
  const createQuote = useMutation(createQuoteAPI, {
    onSuccess: () => {
      queryClient.invalidateQueries('customer');
    },
  });

  const initialStateWidget = {
    width: props.width,
    height: props.height,
    primaryColor: props.primaryColor,
    secondaryColor: props.secondaryColor,
    logo: props.logo,
    currentTab: 1,
    color: 'primary',
    showNotification: false,
  };

  const { width, height } = initialStateWidget;
  const initialStateCustomer = {
    extCustomerName: props.extCustomerName,
    externalID: props.extCustomerID,
    buildingArea: props.buildingArea,
    address: props.address,
    electricalInspectionDate: props.electricalInspectionDate,
    tenantOccupation: props.tenantOccupation,
  };

  const [customerState, dispatchCustomer] = useReducer(
    customerReducer,
    initialStateCustomer,
    () => ({ ...initialStateCustomer }),
  );
  const [widgetState, dispatchWidget] = useReducer(
    widgetReducer,
    initialStateWidget,
    () => ({ ...initialStateWidget }),
  );

  const {
    data: customerData,
    isLoading: customerLoading,
    isError: customerError,
  } = useQuery('customer', () => getCustomer(customerState.externalID));

  const showNewQuote = () => {
    dispatchWidget({ type: 'SET_NOTIFICATION', payload: true });
    dispatchWidget({ type: 'SET_CURRENT_TAB', payload: 3 });
  };

  function createNewQuote() {
    createQuote.mutate({
      customerID: customerState?.externalID,
      buildingArea: customerState?.buildingArea,
      electricalInspectionDate: customerState?.electricalInspectionDate,
      address: customerState?.address,
      tenantOccupation: customerState?.tenantOccupation,
    });
  }

  useEffect(() => {
    if (customerLoading) return;

    const customer = customerData?.data?.data;
    const {
      profile: custProfile,
      quote: custQuotes,
      policy: custPolicies,
    } = customer?.Items;

    if (customer?.Count > 0 && custProfile?.length > 0) {
      if (custQuotes?.length > 0) {
        dispatchInsurance({
          type: 'SET_QUOTES',
          payload: custQuotes,
        });
      } else {
        dispatchInsurance({ type: 'SET_QUOTES', payload: [] });
        createNewQuote();
      }

      if (custPolicies?.length > 0) {
        dispatchInsurance({
          type: 'SET_POLICIES',
          payload: custPolicies,
        });
      } else {
        dispatchInsurance({ type: 'SET_POLICIES', payload: [] });
        dispatchWidget({ type: 'SET_NOTIFICATION', payload: false });
      }
    } else {
      dispatchInsurance({ type: 'CLEAR_DATA' });
      dispatchWidget({ type: 'SET_NOTIFICATION', payload: false });

      if (customerState?.externalID) {
        const res = createCustomer.mutate({
          id: customerState.externalID,
          customerName: customerState.extCustomerName,
        });
      }
    }
  }, [customerData]);

  useEffect(() => {
    if (latestQuote?.Data?.createdDate && latestPolicy?.Data?.createdDate) {
      const latestQuoteDate = new Date(latestQuote?.Data?.createdDate);
      const latestPolicyDate = new Date(latestPolicy?.Data?.createdDate);
      const compareResult = compareAsc(latestQuoteDate, latestPolicyDate);
      if (compareResult === 1) showNewQuote();
    }
  }, [latestQuote, latestPolicy]);

  useEffect(() => {
    if (latestQuote?.Data) {
      const {
        tenantOccupation,
        electricalInspectionDate,
        buildingArea,
        address,
      } = latestQuote?.Data;
      if (
        tenantOccupation !== customerState?.tenantOccupation ||
        electricalInspectionDate !== customerState?.electricalInspectionDate ||
        buildingArea !== customerState?.buildingArea ||
        address !== customerState?.address
      ) {
        createNewQuote();
      }
    }
  }, [latestQuote]);

  const getTabComponent = () => {
    switch (widgetState.currentTab) {
      case 1:
        return (
          <Policy
            logo={widgetState?.logo}
            primaryColor={widgetState?.primaryColor}
            customerName={customerState.extCustomerName}
            customerID={customerState.externalID}
            loading={customerLoading}
            quoteLoading={createQuote?.isLoading}
          />
        );
      case 2:
        return <Claims color={widgetState?.primaryColor} />;
      case 3:
        return (
          <Notifications
            customerName={customerState.extCustomerName}
            customerID={customerState.externalID}
            logo={widgetState?.logo}
            show={widgetState?.showNotification}
            dispatchWidget={dispatchWidget}
            color={widgetState?.primaryColor}
          />
        );
      default:
        return <h1>Tab 1</h1>;
    }
  };

  const setTab = (e, tab) => {
    e.preventDefault();
    dispatchWidget({ type: 'SET_CURRENT_TAB', payload: tab });
  };

  React.useEffect(() => {
    console.log(
      'Changing theme.',
      widgetState.primaryColor,
      widgetState.secondaryColor,
    );
    const baseTheme = {
      '--theme-primary': widgetState.primaryColor
        ? widgetState.primaryColor
        : 'purple',
      '--theme-secondary': widgetState.secondaryColor
        ? widgetState.secondaryColor
        : 'white',
    };
    const root = document.documentElement;
    Object.keys(baseTheme).forEach((cssVar) => {
      root.style.setProperty(cssVar, baseTheme[cssVar]);
    });
  }, [widgetState.primaryColor, widgetState.secondaryColor]);

  return (
    <div
      className="m-auto flex flex-col align-center justify-center relative overflow-hidden kayna-app rounded-3xl"
      style={{
        width: width,
        height: height,
        border: '1px solid #E8E8E8',
      }}
    >
      <div>
        <Suspense
          fallback={
            <div className="h-5" style={{ backgroundColor: '#F8F8F9' }} />
          }
        >
          <ul
            className="flex mb-0 list-none flex-row"
            role="tablist"
            style={{
              width: width,
              backgroundColor: 'white',
            }}
          >
            <Tab
              name="Your Policy"
              currentTab={widgetState.currentTab}
              number={1}
              setTab={setTab}
              // icon={faShieldAlt}
              primaryColor={widgetState?.primaryColor}
            />
            <Tab
              name="Claims"
              currentTab={widgetState.currentTab}
              number={2}
              setTab={setTab}
              // icon={faExclamationCircle}
              primaryColor={widgetState?.primaryColor}
            />
            <Tab
              currentTab={widgetState.currentTab}
              number={3}
              setTab={setTab}
              icon={faBell}
              showNoti={widgetState?.showNotification}
              primaryColor={widgetState?.primaryColor}
              loading={latestPolicy && createQuote?.isLoading}
            />
            {/* <Tab
                name="Options"
                currentTab={widgetState.currentTab}
                number={4}
                setTab={setTab}
                icon={faUser}
              /> */}
          </ul>
        </Suspense>
      </div>
      <div className="antialiased text-center bg-white relative w-full h-full flex flex-col justify-start overflow-y-scroll pt-4">
        {getTabComponent()}
      </div>
      <Suspense
        fallback={
          <div className="h-5" style={{ backgroundColor: '#F8F8F9' }} />
        }
      >
        <Footer />
      </Suspense>
    </div>
  );
}

const QueryWrappedApp = (props) => {
  return (
    <QueryClientProvider client={queryClient}>
      <InsuranceProvider>
        <App {...props} />
      </InsuranceProvider>
    </QueryClientProvider>
  );
};

export default QueryWrappedApp;

