import axios from 'axios';

const API_URL = `https://api.kayna.io/api`;
const AUTH_TOKEN = 'B341G@234face3434334';

let instance = axios.create({
  baseURL: API_URL,
  headers: {
    common: {
      // can be common or any other method
      Authorization: AUTH_TOKEN,
    },
  },
});

export async function getCustomer(externalID) {
  return instance({
    method: 'GET',
    url: `/customer/${externalID}?by=external`,
  });
}

export async function createCustomer({ id, customerName }) {
  console.log('CREATE CUSTOMER CALLED');
  // setLoading(true);
  return instance({
    method: 'POST',
    url: `/customer`,
    data: {
      extCustomerID: id,
      extCustomerName: customerName,
    },
  });
}

export async function handlePurchase({ customerID, customerName, quoteId }) {
  return instance({
    method: 'POST',
    url: '/policy',
    data: {
      extCustomerID: customerID,
      customerName: customerName,
      quoteID: quoteId,
    },
  });
}

export function createQuote({
  customerID,
  buildingArea,
  address,
  electricalInspectionDate,
  tenantOccupation,
}) {
  return instance({
    method: 'POST',
    url: '/quote',
    data: {
      extCustomerID: customerID,
      buildingArea,
      address,
      electricalInspectionDate,
      tenantOccupation,
    },
  });
}
