import React, { useContext } from 'react';
import { useMutation, useQueryClient } from 'react-query';
import { handlePurchase } from '../../api';
import { useInsurance } from '../../context/Insurance';
import shade from '../../utils/shade';
import CardLoader from '../CardLoader';

export default function Policy({
  logo,
  primaryColor,
  customerID,
  customerName,
  loading,
  quoteLoading,
}) {
  const queryClient = useQueryClient();
  const createPolicy = useMutation(handlePurchase, {
    onSuccess: () => {
      queryClient.invalidateQueries('customer');
    },
  });
  const { latestQuote, quotes, policies, latestPolicy } = useInsurance();

  console.log('Insurance: ', { latestQuote, latestPolicy, quotes, policies });
  console.log('Quote purchase: ', createPolicy);

  const purchaseQuote = () => {
    if (latestQuote?.SecondaryID)
      createPolicy.mutate({
        quoteId: latestQuote?.SecondaryID,
        customerID,
        customerName,
      });
  };

  if (quoteLoading && !latestPolicy && !latestQuote)
    return <CardLoader color={primaryColor} text="Generating quote..." />;
  if (createPolicy.isLoading)
    return <CardLoader color={primaryColor} text="Creating policy..." />;

  if (loading || !latestQuote) return <CardLoader color={primaryColor} />;

  return (
    <div className="pb-5 y-2 mx-4 my-auto">
      {latestQuote !== undefined && !Boolean(latestPolicy) && (
        <div
          className={`rounded-3xl mx-2 mt-30 pl-1 pr-1 pb-8 pt-4 bg-white my-2 flex flex-col h-full justify-center`}
        >
          <div className="flex flex-row items-center justify-start">
            {logo && (
              <img src={logo} alt="logo" className="ml-10 getInsuredImg" />
            )}
            <span
              className={`text-2xl font-semibold font-bold text-center w-full ${
                !logo && 'mb-4'
              }`}
            >
              Get protection as low as
            </span>
          </div>
          <div>
            <span className="text-5xl font-bold	text-green-500">
              &#163; {latestQuote?.Data?.quoteExpense?.toFixed(2)}
            </span>
          </div>
          <div className="mt-3">
            <span className="text-base text-gray-500">
              The protection you need, when you need it
            </span>
          </div>

          <button
            className={`rounded-2xl bg-primary ripple text-primary font-bold mt-4 tracking-lighter px-8 py-4`}
            style={{ backgroundColor: shade(primaryColor, 0.9) }}
            onClick={() => {
              purchaseQuote();
            }}
          >
            Purchase Policy
          </button>
        </div>
      )}
      {latestPolicy && (
        <div
          className={`rounded-3xl mx-2 mt-30 my-2 flex flex-col h-full justify-center`}
        >
          <div className="flex flex-row items-center justify-start">
            {logo && (
              <img src={logo} alt="logo" className="ml-10 getInsuredImg" />
            )}
            {latestPolicy?.Data?.customerName && (
              <span
                className={`text-3xl font-bold text-center w-full`}
                style={logo && { transform: 'translateX(-3rem)' }}
              >
                {latestPolicy?.Data?.customerName}
              </span>
            )}
          </div>
          <div
            className="rounded-3xl text-lg m-auto py-4 px-16 my-8"
            style={{ backgroundColor: '#F9F9F9', width: 'fit-content' }}
          >
            <div>
              <div className="font-bold text-lg">Policy Number</div>
              <div>{latestPolicy?.SecondaryID?.substring(7, 25)}</div>
            </div>
          </div>
          <button
            className={`rounded-2xl ripple bg-primary text-primary font-bold tracking-lighter`}
            style={{ backgroundColor: shade(primaryColor, 0.9) }}
          >
            <a
              href={latestPolicy?.Data.policyDocURL}
              download
              className="block px-8 py-4"
            >
              Download Policy Document
            </a>
          </button>
        </div>
      )}
    </div>
  );
}
